<template>
  <v-container justify="center">
    <v-flex>
      <v-card>
        <v-card-title class="justify-center">Mis Contratos</v-card-title>
        <v-card-text>
          <v-data-table
                :headers="headers"
                :items="dataConsulta"
                :search="search"
                class="elevation-1"
            >
              <template v-slot:item.docreqcont="{ item }">
                <v-icon
                  class="mr-5"
                  color="primary"
                  @click="abrirDialogoDocRequerida(item.id)"
                  v-if="item.enviodocreq == 1"
                >
                  file_upload
                </v-icon>
              </template>

              <template v-slot:item.doctrapol="{ item }">
                <v-icon
                  class="mr-5"
                  color="primary"
                  @click="abrirDialogoDocPoliza(item.id)"
                  v-if="item.minutaOK > 0"
                >
                  file_upload
                </v-icon>
              </template>

              <template v-slot:item.doclegcont="{ item }">
                <v-icon
                  class="mr-5"
                  color="primary"
                  @click="abrirDialogoDocLegalizaContrato(item.id)"
                  v-if="item.minutaOK > 0"
                >
                  file_upload
                </v-icon>
              </template>
              <template v-slot:item.docMinuta="{ item }">
                <v-icon
                  class="mr-5"
                  color="primary"
                  @click="abrirDialogoMinuta(item.id)"
                  v-if="item.minutaOK > 0"
                >
                  archive
                </v-icon>
              </template>
              <template v-slot:item.docFinContrato="{ item }">
                <v-icon
                  class="mr-5"
                  color="primary"
                  @click="abrirArcAdicionales (item)"
                  v-if="item.totalmin > 0"
                >
                  file_upload
                </v-icon>
              </template>
            </v-data-table>
        </v-card-text>
      </v-card>
    </v-flex>
    <v-dialog v-model="dialog_docrequerida" max-width="1500px">
      <v-card>
          <v-card-title>
            <span class="headline">{{ titulodialogo }}</span>
          </v-card-title>
          <v-card-text>
              <TabContratosDocRequerida
                :idregistro.sync = "dataRegistro.codigo"
                :tipodoc.sync = "tipodoc"
                :idpropconv.sync = "idpropconv" />
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog_docrequerida=false">Cancelar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog_minuta" max-width="600px">
      <v-card>
          <v-card-title>
            <span class="headline">Minuta del contrato</span>
          </v-card-title>
          <v-card-text>
            <TabMinuta
                :idpropconv.sync = "idpropconv" />
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog_minuta=false">Cancelar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog_arcAdiconales" max-width="800px">
      <v-card>
          <v-card-title>
            <span class="headline">Archivos finalización proceso</span>
          </v-card-title>
          <v-card-text>
              <TabArcAdicionales
                :idpropconv.sync = "idpropconv" />
          </v-card-text>
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog_arcAdiconales=false">Cancelar</v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
export default {
  name: 'miscontratos',
  props: ['codigo', 'estadotxt', 'estado'],
  components: {
    TabContratosDocRequerida: () => import('./tabcontratosDocRequerida'),
    TabMinuta: () => import('./tabMinuta'),
    TabArcAdicionales: () => import('./tabArchivosAdicionales')
  },
  data () {
    return {
      busEstado: 'Adjudicado',
      dataRegistro: [],
      dataConsulta: [],
      search: '',
      dialog_docrequerida: false,
      tipodoc: '',
      idpropconv: '',
      titulodialogo: '',
      dialog_minuta: false,
      dialog_arcAdiconales: false,
      headers: [
        { text: 'Título del proceso', align: 'left', value: 'titulo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha límite', align: 'left', value: 'fecha_cierre_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Publicado', align: 'left', value: 'fecha_apertura_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Tipo de proceso', align: 'left', value: 'tipo_contratacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado', align: 'left', value: 'estado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Referencia', align: 'left', value: 'referencia', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentación requerida para la contratación', align: 'center', value: 'docreqcont', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Minuta del contrato', align: 'center', value: 'docMinuta', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentos finalizar proceso', align: 'center', value: 'docFinContrato', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentación para trámite de poliza', align: 'center', value: 'doctrapol', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Documentación legalización contractual', align: 'center', value: 'doclegcont', class: 'subtitle-1 font-weight-bold black--text' }

      ]
    }
  },
  methods: {
    abrirDialogoDocRequerida (idpropconv) {
      this.titulodialogo = 'Documentación requerida para la contratación'
      this.tipodoc = 'RC'
      this.idpropconv = idpropconv
      this.dialog_docrequerida = true
    },
    abrirDialogoDocPoliza (idpropconv) {
      this.titulodialogo = 'Documentación para trámite de póliza'
      this.idpropconv = idpropconv
      this.tipodoc = 'TP'
      this.dialog_docrequerida = true
    },
    abrirDialogoDocLegalizaContrato (idpropconv) {
      this.titulodialogo = 'Documentación legalización contractual'
      this.idpropconv = idpropconv
      this.tipodoc = 'LC'
      this.dialog_docrequerida = true
    },
    async realizarConsulta () {
      this.dataConsulta = []
      try {
        const { data } = await anunciosProcAdquisicion.buscarPropuesta({
          titulo: '',
          descripcion: '',
          referencia: '',
          fecha_apertura_ini: '',
          fecha_apertura_fin: '',
          fecha_cierre_ini: '',
          fecha_cierre_fin: '',
          estado: 'Adjudicado',
          tipo_contratacion: '',
          pubfreid: this.dataRegistro.codigo,
          adjudicadoproveedor: '1',
          finalizado: '1'
        })
        this.dataConsulta = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    abrirDialogoMinuta (id) {
      this.idpropconv = id
      this.dialog_minuta = true
    },
    abrirArcAdicionales (item) {
      this.idpropconv = item.id
      this.dialog_arcAdiconales = true
    }
  },
  watch: {
    '$store.state.dataRegistro': {
      handler: function (nv) {
        if (nv) {
          this.dataRegistro = nv
          this.realizarConsulta()
        }
      },
      immediate: true // provides initial (not changed yet) state
    }
  },
  mounted () {
  }
}
</script>
